* {
    padding: 0;
    margin: 0;
    font-family: Iceberg, serif;
    font-weight: 600;
}

body {
    box-sizing: border-box;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-size: cover;          
    background-repeat: no-repeat;    
    background-position: center;     
    background-attachment: fixed;
}

dialog {
  transition:
    display 0.3s allow-discrete,
    overlay 0.3s allow-discrete,
    opacity 0.3s;
  opacity: 0;
  
  &[open] {
    opacity: 1;
	  
    @starting-style {
      opacity: 0;
    }
  }
}

dialog::backdrop {
  backdrop-filter: blur(10px);
}

dialog {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 15px;
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    width: 20%;
    min-width: 15rem;
    margin: auto;
    padding: 12px;
}

.projectTodoFormBtn>button,
.projectFormBtns>button,
.formBtns>button {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 15px;
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    padding: 2px 4px 2px 4px;
    font-size: 1rem;
}

#todoForm,
#projectForm,
#projectTodoForm {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: start;
    gap: 6px;
}

.titleInput,
.descriptionInput,
.dueDateInput,
.prioritySelection,
.projectSelection,
.formBtns,
.projectFormBtns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

}

.titleInput>input,
.descriptionInput>input,
.dueDateInput>input {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    outline: none;
    background-color: rgba(255, 255, 255, 0.2);
}

.prioritySelection>select,
.projectSelection>select {
    background-color: rgba(255, 255, 255, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    outline: none;
}

.mid {
    width: min(95%, 1800px);
    height: auto;
    min-height: 90vh;
    display: flex;
    justify-content: center;
    gap: 4px;
}

.sidebar {
    width: 50%;
    margin: 4px;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(3px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    padding-left: 20px;
    overflow-x: auto;
    overflow-y: auto;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}


.sidebar>div:first-child {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.sidebar>div>p {
    font-size: 1.2rem;
}

.projectList>li {
    background-color: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    /* margin: 5px; */
    padding: 8px;
    margin-top: 4px;
    margin-bottom: 4px;
    margin-right: 4px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.projectList li ul {
    padding-left: 12px;
}

.projectList>li>div>p {
    font-size: 1.1rem;
}

.projectContainer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 12px;
}

.projectList li ul li div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.projectList li ul li div div:first-child {
    display: flex;
    justify-content: space-between;
    gap: 24px;
}

.projectList li ul li div div p:first-child {
    width: 10ch;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.projectList li ul li div div p:nth-child(2) {
    width: 15ch;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.projectList li ul li div div p:nth-child(3) {
    width: 14ch;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.projectList li ul li {
    margin-top: 4px;
    margin-bottom: 4px;
    margin-right: 4px;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    transition: transform 0.2s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.projectList li ul li:hover {
    transform: scale(1.02);
}


.strike {
    text-decoration: line-through;
    text-decoration-color: black;
    text-decoration-style: solid;
    text-decoration-thickness: 3px;
}

.todoControls {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 8px;
    margin-right: 8px;
}

.todoControls select {
    background-color: rgba(255, 255, 255, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    outline: none;
}


.todoControls input {
    width: 20px;
    height: 20px;
}


.main {
    width: 50%;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(3px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    overflow-y: auto;
    margin: 4px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}


.creator {
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    grid-template-rows: 1fr;
    gap: 15px;
    justify-items: center;
    align-items: flex-start;
}

.mainTodo {
    background-color: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    width: 15ch;
    padding: 0.5em;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    transition: transform 0.2s ease;
}

.mainTodo:hover {
    transform: scale(1.10);
}

.mainTodo:active {
    transform: scale(0.9);
}

.mainTodo h3 {
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.mainTodo h4 {
    text-align: center;
}

.projectContainer button,
.todoControls button {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    width: 22px;
    height: 22px;
    border: transparent;
    transition: transform 0.2s ease;
}

.projectContainer button:hover,
.todoControls button:hover {
    transform: scale(1.20);
}

.projectContainer button:active,
.todoControls button:active {
    transform: scale(0.90);
}

.creator>button {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    width: 42px;
    height: 42px;
    border: transparent;
    transition: transform 0.2s ease;
}

.creator>button:hover {
    transform: scale(1.30);
}

.creator>button:active {
    transform: scale(0.8);
}

.sidebar>div>button {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    width: 22px;
    height: 22px;
    border: transparent;
    transition: transform 0.2s ease;
}

.sidebar>div>button:hover {
    transform: scale(1.30);
}

.sidebar>div>button:active {
    transform: scale(0.8);
}

footer>button {
    font-size: 1.05rem;
    background-color: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 5px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    transition: transform 0.2s ease;
}

footer>button:hover {
    transform: scale(1.15);
}

footer>button:active {
    transform: scale(0.9);

}

footer>div {
    background-color: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 5px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    
}

footer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 8px;
    margin-top: 8px;
}

header>div {
    background-color: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 5px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.critical {
    color: red;
}

.high {
    color: orangered;
}

.moderate {
    color: orange;
}

.low {
    color: green;
