idth: min(95%, 1800px);\n    height: auto;\n    min-height: 90vh;\n    display: flex;\n    justify-content: center;\n    gap: 4px;\n}\n\n.sidebar {\n    width: 50%;\n    margin: 4px;\n    background-color: rgba(255, 255, 255, 0.2);\n    backdrop-filter: blur(3px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 10px;\n    padding-left: 20px;\n    overflow-x: auto;\n    overflow-y: auto;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);\n}\n\n\n.sidebar>div:first-child {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    gap: 10px;\n}\n\n.sidebar>div>p {\n    font-size: 1.2rem;\n}\n\n.projectList>li {\n    background-color: rgba(255, 255, 255, 0.3);\n    backdrop-filter: blur(5px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 10px;\n    /* margin: 5px; */\n    padding: 8px;\n    margin-top: 4px;\n    margin-bottom: 4px;\n    margin-right: 4px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);\n}\n\n.projectList li ul {\n    padding-left: 12px;\n}\n\n.projectList>li>div>p {\n    font-size: 1.1rem;\n}\n\n.projectContainer {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    gap: 12px;\n}\n\n.projectList li ul li div {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    \n}\n\n.projectList li ul li div div:first-child {\n    display: flex;\n    justify-content: space-between;\n    gap: 24px;\n}\n\n.projectList li ul li div div p:first-child {\n    width: 10ch;\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n.projectList li ul li div div p:nth-child(2) {\n    width: 15ch;\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n.projectList li ul li div div p:nth-child(3) {\n    width: 14ch;\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n.projectList li ul li {\n    margin-top: 4px;\n    margin-bottom: 4px;\n    margin-right: 4px;\n    background-color: rgba(255, 255, 255, 0.5);\n    backdrop-filter: blur(10px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 10px;\n    transition: transform 0.2s ease;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);\n}\n\n.projectList li ul li:hover {\n    transform: scale(1.02);\n}\n\n\n.strike {\n    text-decoration: line-through;\n    text-decoration-color: black;\n    text-decoration-style: solid;\n    text-decoration-thickness: 3px;\n}\n\n.todoControls {\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 8px;\n    margin-right: 8px;\n}\n\n.todoControls select {\n    background-color: rgba(255, 255, 255, 0.3);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 10px;\n    outline: none;\n}\n\n\n.todoControls input {\n    width: 20px;\n    height: 20px;\n}\n\n\n.main {\n    width: 50%;\n    background-color: rgba(255, 255, 255, 0.2);\n    backdrop-filter: blur(3px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 10px;\n    overflow-y: auto;\n    margin: 4px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);\n}\n\n\n.creator {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.content {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));\n    grid-template-rows: 1fr;\n    gap: 15px;\n    justify-items: center;\n    align-items: flex-start;\n}\n\n.mainTodo {\n    background-color: rgba(255, 255, 255, 0.3);\n    backdrop-filter: blur(5px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 10px;\n    width: 15ch;\n    padding: 0.5em;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);\n    transition: transform 0.2s ease;\n}\n\n.mainTodo:hover {\n    transform: scale(1.10);\n}\n\n.mainTodo:active {\n    transform: scale(0.9);\n}\n\n.mainTodo h3 {\n    text-align: center;\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n.mainTodo h4 {\n    text-align: center;\n}\n\n.projectContainer button,\n.todoControls button {\n    background: url(./img/delete.svg);\n    width: 22px;\n    height: 22px;\n    border: transparent;\n    transition: transform 0.2s ease;\n}\n\n.projectContainer button:hover,\n.todoControls button:hover {\n    transform: scale(1.20);\n}\n\n.projectContainer button:active,\n.todoControls button:active {\n    transform: scale(0.90);\n}\n\n.creator>button {\n    background: url(./img/plus.svg);\n    width: 42px;\n    height: 42px;\n    border: transparent;\n    transition: transform 0.2s ease;\n}\n\n.creator>button:hover {\n    transform: scale(1.30);\n}\n\n.creator>button:active {\n    transform: scale(0.8);\n}\n\n.sidebar>div>button {\n    background: url(./img/plus.svg);\n    width: 22px;\n    height: 22px;\n    border: transparent;\n    transition: transform 0.2s ease;\n}\n\n.sidebar>div>button:hover {\n    transform: scale(1.30);\n}\n\n.sidebar>div>button:active {\n    transform: scale(0.8);\n}\n\nfooter>button {\n    font-size: 1.05rem;\n    background-color: rgba(255, 255, 255, 0.3);\n    backdrop-filter: blur(5px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 5px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);\n    transition: transform 0.2s ease;\n}\n\nfooter>button:hover {\n    transform: scale(1.15);\n}\n\nfooter>button:active {\n    transform: scale(0.9);\n\n}\n\nfooter>div {\n    background-color: rgba(255, 255, 255, 0.3);\n    backdrop-filter: blur(5px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 5px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);\n    \n}\n\nfooter {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n    gap: 8px;\n    margin-top: 8px;\n}\n\nheader>div {\n    background-color: rgba(255, 255, 255, 0.3);\n    backdrop-filter: blur(5px);\n    border: 1px solid rgba(255, 255, 255, 0.3);\n    border-radius: 5px;\n    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);\n}\n\n.critical {\n    color: red;\n}\n\n.high {\n    color: orangered;\n}\n\n.moderate {\n    color: orange;\n}\n\n.low {\n    color: green;\n}"],"sourceRoot":""}]);
